
import React from "react";

import "./styles.css"
import { CloseModal } from "../../../components/DialogHRM";




const DeleteAlert  = ({ data, confirm, label, close }) => {

	const { modalID } = data;

	const handleClose =  (v) => {
		CloseModal({id:modalID})
    }
    
    const handleConfirm =  () => {
		
        confirm();
		handleClose();
	}



    return (
       
		<div style={{
			maxHeight: "40vh",
			minHeight:  "28vh",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					{label}
				</p>
				<div className="flexSpace"/>
				
			</div>
			<div className="_dsplFlx ">
			<span>
					Una vez eliminado no podra ser recuperado
				</span>
			</div>
			<div className="_dsplFlx " style={{padding:"15px 10px"}}>
				<div className="flexSpace"/>
				<p className="group_title addBtn" onClick={handleClose}>
					Cancelar
				</p>
				<p className="group_title addBtn red" onClick={handleConfirm}>
					Eliminar
				</p>
			</div>

		</div>
    )   
}

export default DeleteAlert;


