import React, { useEffect, useState, useRef } from "react";

import { getStore, updStore, gen16CodeId, copyToClipboard, hasWritePrivilege } from "../../../stores/Utils";

import "./dashboard.css"
import {  UpdProduct_Cmpt,   ViewSubMayor_Cmpt } from "..";
import { OpenModal } from "../../../components/DialogHRM";
import { getall_product, sortProducts, delete_product, get_inventory_by_prod } from "../services/server_fetching";
import { SvgIcon_Cmpt, BoxImageSquare_Cmpt, ButtonImageRound_Cmpt } from "../../../components";
import DeleteAlert from "./deleteAlert";
import NoPrivilege from "./NoPrivilege";


const Svg = SvgIcon_Cmpt();

const UpdProduct = UpdProduct_Cmpt();

const ViewSubMayor = ViewSubMayor_Cmpt();


// const BoxImageSquare = BoxImageSquare_Cmpt();
// <BoxImageSquare src={data?. imageUrl} />
const ButtonImageRound = ButtonImageRound_Cmpt();


const ProductItem  = ({ data, elmId, handleRefreshAll }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			// let frm2 = {"firstName":"Belisario","lastName":"Gonzalez Arevalo","genre":"Male","dateOfBirth":"06/02/1976","placeOfBirth":{"city":"Holguin","state":"Holguin"},"currentAddress":{"No":"4307","street":"Estate Dr","city":"Louisville","state":"Kentucky","zipcode":"40216"},"dob":202536000000}
			//updStore(frmNm, frm2);
			set_obs(gen16CodeId())
		}
	});

	
	const updObs =  () => {
		set_obs(gen16CodeId())
	}



	const refreshAccs =  () => {
		getall_product(updObs)
	}

	const update = async () => {
		if(hasWritePrivilege()){
			updStore("upd_product", data);
			updStore("upd_product_bck", JSON.parse(JSON.stringify(data)));
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "80vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true };
			modaldata["content"] = <UpdProduct confirm={refreshAccs} />;
			OpenModal(modaldata);
		}
	}

	
	


	const openSubMayor =  (sba) => {
		

		let modaldata = {};
		modaldata["zIndex"] = 450;
		modaldata["height"] = "80vh";
		// data['observeResize']=true;
		modaldata["props"] = { minHeight: "1vh", overlay: true };
		modaldata["content"] = 
			<ViewSubMayor confirm={refreshAccs} item={data}/>
		OpenModal(modaldata);
	}


	const deleteComprobante =  () => {
		delete_product(data?.productId, handleRefreshAll);
	}
	

	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(hasWritePrivilege()){
			if( Date.now() > lastTimeCallModal){
				updStore("lastTimeCallModal", Date.now()+320);
				let modaldata = {};
				modaldata["zIndex"] = 450;
				modaldata["height"] = "40vh";
				// data['observeResize']=true;
				modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
				modaldata["content"] = <DeleteAlert  label={"Eliminar Producto"} confirm={deleteComprobante} item={data} />
				OpenModal(modaldata);
			}
		}else{
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <NoPrivilege />
			OpenModal(modaldata);
		}
	}

	

	let subAcc = sortProducts( getStore("sub_product_group")?.[data.productId],"subProduct");
	
	
	const copyToClpb  =  ()  => {
		copyToClipboard( data?. productCode);
		//window.open("https://www.upcitemdb.com/upc/"+data?. productCode);
		//window.open("https://www.buycott.com/upc/"+data?. productCode);
		
		
	};


	// 041331040082
	// 041331091169
	// 041331090681


	return (
		<div className="item_acc">
			<div className="_dsplFlx item_box product_header">
			
				<div className="_dsplFlx">
					<div className="code" onClick={copyToClpb}>
					
					</div>
					<div className="name"   onClick={update}>
						{data?. name}
					</div>
					
				</div>
				<div className="flexSpace"/>
				<div className=" addSbAcc purple" onClick={()=>openSubMayor(null)}>
					Submayor
				</div>
				{hasWritePrivilege()?
				<div className="icon" style={{marginLeft: 19}} onClick={OpenDeleteAlert}>
					<Svg name={"outline_delete"} color={"#c62828"}/>
				</div>
				:null}
			</div>
			
		</div>
	)
}
export default ProductItem;